import { useParams, useNavigate } from "@solidjs/router";
import { View, Text } from "@lightningjs/solid";
import { Column, Row, Button } from "@lightningjs/solid-ui";
import { createSignal, createEffect } from "solid-js";
import { getBackdropUrl, getPosterUrl } from "@velope/ott-o-rama-core/media";

import ItemDetailStyles from "./ItemDetail.styles";
import colors from "../../utils/colors";

export default function ItemDetail(props) {
    const navigate = useNavigate();
    const params = useParams();
    const [poster, setPoster] = createSignal('')
    const [backdrop, setBackdrop] = createSignal('')

    createEffect(() => {
        setPoster(getPosterUrl(props.poster_path, 300))
    })

    createEffect(() => {
        setBackdrop(getBackdropUrl(props.backdrop_path, window.innerWidth))
    })

    function onEnter() {
        navigate(`/video`)
    }

    return (
        <>
            <View
                {...props}
                style={ItemDetailStyles.Container}
                x={0}
                src={backdrop()}
            >
                <View
                    style={ItemDetailStyles.Poster}
                    src={poster()}
                />
                <View style={{
                    y: 500,
                    x: 0,
                    width: 1920,
                    height: 100,
                    color: colors.background,

                }}>
                    <Row
                        x={550}
                        y={25}
                        gap={40}
                        scroll="none"
                    >
                        <Button autofocus width={150} height={50} style={ItemDetailStyles.Button} onEnter={onEnter}>Play</Button>
                        <Button width={150} height={50} style={ItemDetailStyles.Button} onEnter={() => history.back()}>Back</Button>
                    </Row>
                </View>
                <Column
                    y={620}
                    width={1920}
                    height={480}
                    color={colors.teal}
                >
                    <Text
                        x={550}
                        y={20}
                        fontSize={36}
                        marginTop={20}
                    >
                        {props.name}
                    </Text>
                    <Text
                        x={550}
                        width={900}
                        fontSize={24}
                        contain="width"
                        lineHeight={30}
                    >
                        {props.overview}
                    </Text>

                </Column>
            </View>
        </>
    );
}