import { render, Config } from '@lightningjs/solid';
import { HashRouter, Route } from "@solidjs/router";
import App from './pages/App';
import Home from './pages/Home/Home';
import DetailsPage from './pages/DetailsPage/DetailsPage'
import PlayerPage from './pages/PlayerPage/PlayerPage'
import NotFound from './pages/NotFound';

import coreExtensionModuleUrl from './AppCoreExtensions.js?importChunkUrl';

Config.debug = false;
Config.fontSettings.fontFamily = 'Ubuntu';
Config.fontSettings.color = 0xffffffff;
Config.rendererOptions = {
  coreExtensionModule: coreExtensionModuleUrl,
  numImageWorkers: 2,
  deviceLogicalPixelRatio: window.innerWidth === 1280 ? 2 / 3 : 1,
  enableInspector: true,
  boundsMargin: 20,
};

render(() => (
  <HashRouter root={App}>
    <Route path="" component={Home} />
    <Route path="/browse/:category" component={Home} />
    <Route path="/details/:type/:id" component={DetailsPage} />
    <Route path="/video" component={PlayerPage} />
    <Route path="/*all" component={NotFound} />
  </HashRouter>
));
