import { View } from "@lightningjs/solid";
import { createSignal, createEffect, onMount } from "solid-js";
import { playPause, shouldPause } from "@velope/ott-o-rama-core/player";

import LoadingSpinner from '../../components/LoadingSpinner/LoadingSpinner';
import colors from "../../utils/colors";
import { initShakaPlayer } from "../../utils/shakaPlayer/shakaPlayer";

const PlayerPage = () => {
    let el
    let playerContainer = document.getElementById('video-container');
    let player = document.getElementById("video-player")

    const [loading, setLoading] = createSignal(false);

    createEffect(() => {
        setLoading(true);
        initShakaPlayer()
        setLoading(false);
    });

    onMount(() => {
        el.setFocus()
    })

    function onKeyPress(e) {
        if (player === null) {
            player = document.getElementById("video-player")
        }
        switch (e.code) {
            case "Enter":
                playPause(player)
                break;
            case "Space":
                playPause(player)
                break;
            case "Backspace":
                if (shouldPause()) {
                    if (playerContainer === null) {
                        playerContainer = document.getElementById('video-container');
                        playerContainer.remove();
                    }
                } else {
                    player.style.display = 'none'
                }
                break;
            default:
                break;
        }
    }

    return (
        (loading()) ?
            <LoadingSpinner /> :
            <>
                <View
                    ref={el}
                    onKeyPress={(e) => onKeyPress(e)}
                    style={{
                        zIndex: 110,
                        color: colors.black
                    }}>
                </View>
            </>
    )
};

export default PlayerPage;