import { View } from "@lightningjs/solid";

import colors from "../../utils/colors";

export default function Background() {
    return (
        <>
            <View width={1920} height={1080} zIndex={-5}>
                <View
                    color={colors.background}
                    width={1920}
                    height={1080}
                />
            </View>
        </>
    );
}