import { View, Text } from '@lightningjs/solid';
import { onMount, onCleanup } from "solid-js";

import LoadingSpinnerStyles from './LoadingSpinner.styles';

const LoadingSpinner = () => {
    let spinner, invervalTimer;
    let rotation = 0;

    onMount(() => {
        invervalTimer = setInterval(() => {
            rotation = (rotation + 10) % 360;
            spinner.rotation = rotation;
        }, 1000);
    });

    onCleanup(() => {
        clearInterval(invervalTimer);
    });

    return <>
        <View style={LoadingSpinnerStyles.LoadingContainer}>
            <View ref={spinner} src='../../assets/spinner.png' style={LoadingSpinnerStyles.Spinner}></View>
            <Text>Loading...</Text>
        </View>
    </>
};

export default LoadingSpinner;