import { View, Text, activeElement } from '@lightningjs/solid';
import { Column, Row } from '@lightningjs/solid-ui';
import { useNavigate, useParams } from "@solidjs/router";
import { createSignal, createEffect, createSelector, For } from "solid-js";
import { getPage } from '@velope/ott-o-rama-core/browser';

import Sidebar from "../../components/Sidebar/Sidebar";
import CarouselItem from '../../components/CarouselItem/CarouselItem';
import LoadingSpinner from '../../components/LoadingSpinner/LoadingSpinner';

import homeStyles from './Home.styles';

const Home = () => {
    let navDrawer, lastFocused;
    let totalAssets = 10
    const navigate = useNavigate()
    const [tmdbRows, setTmdbRows] = createSignal(Array.from({ length: totalAssets }, () => []));
    const [loading, setLoading] = createSignal(false);
    const isFirst = createSelector(() => 0);

    const loadAssets = async (rowIndex) => {
        const resTmdb = await getPage(rowIndex);
        setTmdbRows(prevRows => {
            const newRows = [...prevRows];
            newRows[rowIndex - 1] = resTmdb;
            return newRows;
        });
    }

    createEffect(() => {
        setLoading(true);
        for (let i = 1; i <= totalAssets; i++) {
            loadAssets(i);
        }
        setLoading(false);
    });

    function focusNavDrawer() {
        if (navDrawer.states.has("focus")) {
            return false;
        }
        lastFocused = activeElement();
        return navDrawer.setFocus();
    }

    function onEnter(asset) {
        navigate(`details/${asset.type}/${asset.id}`)
    }

    return <>
        {
            (loading() ?
                <>
                    <LoadingSpinner />
                </> :
                <>
                    <View
                        onLeft={focusNavDrawer}
                        onRight={() => navDrawer.states.has("focus") && lastFocused.setFocus()}
                    >
                        <View
                            style={homeStyles.AppHeader}>
                            <Text style={homeStyles.AppTitle}>Solid Demo App</Text>
                        </View>

                        <Sidebar
                            ref={navDrawer}
                            focusPage={() => lastFocused.setFocus()}
                        />

                        <Column
                            scroll="auto"
                            y={200}
                            x={170}
                            gap={200}
                            style={homeStyles.CarouselAnimation} >
                            <For each={tmdbRows()}>
                                {(row, rowIndex) => (
                                    <Row
                                        style={homeStyles.Row}
                                        justifyContent={"center"}
                                        gap={40}
                                    >
                                        <For each={row}>
                                            {(asset, i) => (
                                                <CarouselItem autofocus={isFirst(i())} index={(i() + 1)} {...asset} onEnter={() => onEnter(asset)} />
                                            )}
                                        </For>
                                    </Row>
                                )}
                            </For>
                        </Column>
                    </View>

                </>
            )
        }
    </>
};

export default Home;