import { useParams } from "@solidjs/router";
import { createSignal, createEffect } from "solid-js";
import { getDetails } from '@velope/ott-o-rama-core/tmdb';

import ItemDetail from '../../components/ItemDetail/ItemDetail';
import LoadingSpinner from '../../components/LoadingSpinner/LoadingSpinner';

const DetailsPage = () => {
    const params = useParams();
    const [assetDetail, setAssetDetail] = createSignal({});
    const [loading, setLoading] = createSignal(false);

    const loadAssetDetails = async (type, id) => {
        const resDetails = await getDetails(type, id);
        setAssetDetail(resDetails)
    }

    createEffect(() => {
        setLoading(true);
        loadAssetDetails(params.type, params.id)
        setLoading(false);
    });

    return ( (loading()) ? <LoadingSpinner /> : <ItemDetail {...assetDetail()} /> )
};

export default DetailsPage;