import colors from "../../utils/colors"

export default {
    AppHeader: {
        width: 1920,
        height: 175,
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'row',
        y: 0,
        zIndex: 99,
        color: colors.background
    },
    AppTitle: {
        textAlign: 'center',
        fontSize: 64,
        y: 50
    },
    LoadingContainer: {
        width: 1920,
        height: 1080,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        zIndex: 100,
    },
    Spinner: {
        width: 150,
        height: 150,
        rotation: 0,
        transition: {
            rotation: { duration: 1000, easing: "ease-in-out" },
        },
    },
    Row: {
        display: 'flex',
        justifyContent: 'spaceBetween',
        width: 1760,
        height: 300,
        gap: 26,
        transition: { x: { duration: 250, easing: "ease-in-out" } }
    },
    Column: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flexStart',
        width: 1760,
        height: 500,
        gap: 50,
        y: 200,
        x: 80,
    },
    CarouselAnimation: {
        transition: {
            y: {
                duration: 250,
                easing: "ease-in-out"
            }
        }
    },
}