import shaka from 'shaka-player';
import { Player as ShakaPlayer, ui as ShakaUI } from "shaka-player/dist/shaka-player.ui";
import shakaPlayerStyles from './shakaPlayer.styles';

import { DEFAULT_VIDEO_URL } from '@velope/ott-o-rama-core/config';

const onError = (error) => {
    console.error('Error code:', error.code, 'object', error);
}

export async function initShakaPlayer() {
    shaka.polyfill.installAll()

    let player
    let video;
    let uriToLoad = "https://storage.googleapis.com/shaka-demo-assets/angel-one/dash.mpd"

    const videoContainer = document.createElement('div');
    videoContainer.id = 'video-container';
    document.body.appendChild(videoContainer);

    if (document.getElementById("video-player") === null) {
        video = document.createElement('video');
        video.id = 'video-player';
    } else {
        video = document.getElementById("video-player")
    }

    // video.setAttribute('crossorigin', 'anonymous');

    videoContainer.appendChild(video);

    Object.assign(video.style, shakaPlayerStyles.videoStyles);

    player = new ShakaPlayer(video);

    window.player = player;

    const ui = new ShakaUI.Overlay(player, videoContainer, video);
    const controls = ui.getControls()

    controls.addEventListener("error", console.error);
    player.addEventListener("error", console.error);

    // Removes a ad spinner that causes an empty blank space down of the page
    const shakaAdSpinner = document.querySelectorAll(".shaka-spinner")[0]
    shakaAdSpinner.remove()

    const shakaControls = document.querySelectorAll(".shaka-controls-container")[0]
    shakaControls.remove()

    try {
        await player.load(uriToLoad);
        video.src = DEFAULT_VIDEO_URL

        // await this.player.load({
        //     streamUrl: DEFAULT_VIDEO_URL,
        //     type: "mp4",
        //     track: "",
        //   })

        const eventManager = new shaka.util.EventManager();

        eventManager.listen(player, 'buffering', (event) => {
            video.style.zIndex = '0';
            if (event.buffering == false) {
                video.style.zIndex = '150';
                video.play()
            }
        });
    } catch (e) {
        onError(e);
    }
}


