import colors from "../../utils/colors";

export default {
  Column: {
    flexDirection: "column",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    color: colors.teal,
    width: 600,
    height: 1080,
    gap: 20,
    zIndex: 101,
    transition: {
      x: {
        duration: 250,
        easing: "ease-in-out",
      },
    },
    x: -450,
    focus: {
      width: 600,
      x: 0,
    },
  },
  SidebarBtn: {
    color: colors.buttonUnfocused,
    focus: {
      color: colors.butonFocused
    }
  }
};
