import { View, Text } from "@lightningjs/solid";
import { createSignal } from "solid-js";
import { getPosterUrl } from "@velope/ott-o-rama-core/media";
import { airDate } from "@velope/ott-o-rama-core/item";

import CarouselItemStyles from "./CarouselItem.styles";
import colors from "../../utils/colors";

export default function CarouselItem(props) {
    const [color, setColor] = createSignal(colors.black);

    const textColor = () => {
        if (color() === colors.white) {
            return colors.black;
        } else {
            return colors.white;
        }
    }

    return (
        <>
            <View
                {...props}
                onFocus={() => setColor(colors.white)}
                onBlur={() => setColor(colors.black)}
                style={CarouselItemStyles.Container}
                src={getPosterUrl(props.poster, 200)}
            >
                <View
                    style={CarouselItemStyles.assetContainer}
                    color={color()}>
                    <Text
                        x={10}
                        y={10}
                        fontSize={20}
                        width={200}
                        height={42}
                        contain="both"
                        color={textColor()}
                    >
                        {airDate(props.airDate)}
                    </Text>
                    <Text
                        x={10}
                        y={40}
                        fontSize={24}
                        width={200}
                        height={42}
                        contain="both"
                        color={textColor()}
                    >
                        {props.name}
                    </Text>
                    <Text
                        x={10}
                        y={70}
                        fontSize={20}
                        width={200}
                        height={42}
                        contain="both"
                        color={textColor()}
                    >
                        {props.voteAverage}
                    </Text>
                </View>
            </View>
        </>
    );
}