import { useMatch, useNavigate } from "@solidjs/router";
import { Text } from "@lightningjs/solid";
import { Column, Button } from "@lightningjs/solid-ui";
import { TITLE } from "@velope/ott-o-rama-core/drawer";

import colors from "../../utils/colors";
import SidebarStyles from "./Sidebar.styles";

export default function Sidebar(props) {
    const navigate = useNavigate();

    function onFocus() {
        this.children.forEach((c) => c.states.add("active"));
        this.children.selected.setFocus();
    }

    function onBlur() {
        this.selected = 0;
        this.children.forEach((c) => c.states.remove("active"));
    }

    const categories = [{
        key: "tv",
        title: "TV Series"
    },
    {
        key: "movie",
        title: "Movies"
    },
    {
        key: "documentaries",
        title: "Documentaries"
    },
    {
        key: "trending",
        title: "Trending"
    }]

    // WIP: Early implementation of browsing to search another categories
    function handleNavigate(page) {
        const isOnPage = useMatch(() => page);
        if (isOnPage()) {
            return props.focusPage();
        }
        navigate(page);
    }

    return (
        <>
            <Column
                {...props}
                onFocus={onFocus}
                onBlur={onBlur}
                style={SidebarStyles.Column}
                scroll="none"
            >
                <Text skipFocus fontSize={42} color={colors.white}>{TITLE}</Text>
                <For each={categories}>
                    {(category, i) => (
                        <Button width={250} height={80} style={SidebarStyles.SidebarBtn} onEnter={() => handleNavigate(`/browse/${category.key}`)}>{category.title}</Button>
                    )}
                </For>
            </Column>
        </>
    );
}